import React, { Component } from "react";
import Button from '@mui/material/Button';
import moment from 'moment';
import MatchCard from './components/MatchCard';
//import helpers from './helpers/APIHelper.js';
import leaguefixtures from './helpers/leaguefixtures.json';
import { Match } from "./model/hattrick/Match";
import './css/App.css';
import SignIn from "./components/SignIn";
import Results from "./components/Results";
import helpers from './helpers/APIHelper.js';
import ScoreBoard from "./components/ScoreBoard";
import { Box } from "@mui/material";
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import SportsSoccerIcon from '@mui/icons-material/SportsSoccer';
import ContentPasteIcon from '@mui/icons-material/ContentPaste';
import { ThemeProvider, createTheme } from "@mui/material/styles";

type MyState = { 
  showResults: boolean, 
  guesses: any, 
  currentUser: any, 
  matches: Match[], 
  round: string, 
  mainRound: string,
  scoreBoard: any,
  totalScoreBoard: any,
  value: any
};
const theme = createTheme({
  palette: {
      mode: "dark"
  }
});
class App extends Component <any, MyState> {
  constructor(props: any) {
    super(props);
    this.state = {
      totalScoreBoard: null,
      scoreBoard: null,
      showResults: false,
      guesses: null,
      currentUser: null,
      matches: [],
      round: '1',
      mainRound: '1',
      value: 0
    }; 
  }
  async componentWillMount() {

    //Logged in stuff
    const token = window.localStorage.getItem("token");
    if(token != null){
      const id = window.localStorage.getItem("id");
      const name = window.localStorage.getItem("name");
      const email = window.localStorage.getItem("email");
      this.setState({currentUser: {
        userName:name,
        userEmail:email,
        userId:id,
        userToken:token
      }}, () => {
        this.updateTotalScoreBoard();
      });
    
    }
    //Get round stuff
    
    var date = new Date(); //get the day
    //let somy = date.setDate(date.getDate() + 2); // to manually test dates
    let dayOfWeek = moment(date).day(); // get the day of week (0 = sunday)
    
    let diff = 6 - dayOfWeek; // = 5 in this test
    let thisWeekMatchDate = dayOfWeek > 0 ? date.setDate(date.getDate() + diff) : date.setDate(date.getDate() - 1); // to manually test dates
    
    let currentMatches = leaguefixtures?.Match.filter(match => {
      
        let test1 = moment(thisWeekMatchDate).format('DD.MM.YY')
        let test2 = moment(match.MatchDate).format('DD.MM.YY')
        return test1 === test2
     
    }); 
    this.setState({ 
      matches: currentMatches, 
      round: currentMatches[0]?.MatchRound, 
      mainRound: currentMatches[0]?.MatchRound 
    });
    if(token != null){
      //Get results stuff
      let guesses = await this.getGuesses(currentMatches[0]?.MatchRound, token)
      this.setState({ 
        guesses: guesses,
      });
    } 
  
  }

  getGuesses (round: string, token: string)  {
    return helpers.fetchMatches(round, token).then((data) => {
      return data;
    });
  }

  logout = () => {
    window.localStorage.removeItem("id");
    window.localStorage.removeItem("name");
    window.localStorage.removeItem("email");
    window.localStorage.removeItem("token");
    this.setState({currentUser:null})

  }

  setUser = async (user: any) => {
    if(user){  
      window.localStorage.setItem("id", user.userId);
      window.localStorage.setItem("name", user.userName);
      window.localStorage.setItem("email", user.userEmail);
      window.localStorage.setItem("token", user.userToken);
      
      let guesses = await this.getGuesses(this.state.matches[0].MatchRound, user.userToken)
      this.setState({ 
        guesses: guesses,
        currentUser: user
      }, () => {
        this.updateTotalScoreBoard();
      });
    }
  }

  updateTotalScoreBoard = async () => {
    const { currentUser } = this.state;
    const users = await helpers.getAllUsers(currentUser.userToken)

    let totalScoreBoard = users.map((user: any) => {
          return {userId: user.userId, userName: user.userName, score: 0}
    })
    const rounds = leaguefixtures?.Match
      .map((match: Match) => {
        return match.MatchRound;
      })
      .filter((elem, index, self) => {
        return index === self.indexOf(elem);
      })
    rounds.forEach(async round => {
      
      let guesses = await this.getGuesses(round, currentUser.userToken)
      guesses?.matches.map((match: any) => {
        match.guesses.map((guess: any) => {
          const points = this.calculateScore(match.matchHomeScore, match.matchAwayScore, guess) ?? -1
          if(points >= 0){
            totalScoreBoard.map((user: any) => {
              if(user.userId === guess.userId){
                user.score += points;
              }
              return user;
            })
            guess.points = points;
          }
          return guess;
        })
        return match;
      })
      totalScoreBoard = totalScoreBoard.sort((a: any, b: any) => (a.score < b.score ? 1 : -1));
      this.setState({totalScoreBoard: totalScoreBoard})
  
    })
  }

  updateScoreBoard = () => {
    const { round, guesses } = this.state;
    if(this.state.guesses?.matches.length === 0){
      this.setState({scoreBoard: null})
      return
    }
    let scoreBoard = {
      round: round,
      scores: this.state.guesses?.matches[0].guesses.map((guess: any) => {
          return {userId: guess.userId, userName: guess.userName, score: 0}
      })
    }
        
    guesses?.matches.map((match: any) => {
      match.guesses.map((guess: any) => {
        const points = this.calculateScore(match.matchHomeScore, match.matchAwayScore, guess) ?? -1
        if(points >= 0){
          scoreBoard.scores.map((user: any) => {
            if(user.userId === guess.userId){
              user.score += points;
            }
            return user;
          })
          guess.points = points;
        }
        return guess;
      })
      return match;
    })
    scoreBoard.scores = scoreBoard.scores.sort((a: any, b: any) => (a.score < b.score ? 1 : -1));
    this.setState({scoreBoard: scoreBoard})
  }

  calculateScore = (home: any, away: any, guess: any) => {      
    if(home === null || home === undefined || isNaN(home)){
      return -1;
    }
    home = parseInt(home);    
    away = parseInt(away);
    guess.homeScore = parseInt(guess.homeScore);
    guess.awayScore = parseInt(guess.awayScore);
    
    const homeWin = home > away;
    const awayWin = away > home;
    const tie = away === home;
    
    const guessHomeWin = guess.homeScore > guess.awayScore;
    const guessAwayWin = guess.homeScore < guess.awayScore;
    const guessTie = guess.homeScore === guess.awayScore;
    
    if(home === guess.homeScore && away === guess.awayScore){
        return 5;
    } else if (tie && guessTie){
        return 4;
    }else if ((homeWin && guessHomeWin) ||(awayWin && guessAwayWin) ){
        if((home - away) === (guess.homeScore - guess.awayScore)){
            return 3;
        }else if(home === guess.homeScore || away === guess.awayScore){
            return 3;
        }
        return 2;
    }else if(home === guess.homeScore || away === guess.awayScore){
            return 1;
    }else {
        return 0
    }

};

  plusRound(){
    const round = parseInt(this.state.round);
    this.setRound('' + (round + 1))
  }

  minusRound(){
    const round = parseInt(this.state.round);
    this.setRound('' + (round - 1))
  }

  showResults(){
    const showResults = this.state.showResults;
    this.setState({showResults: !showResults});
  }

  getResult(realMatch: any, guesses: any){
    let result = guesses.matches.filter((match: any) => match.matchId === realMatch.MatchID);

    return result[0];
  }

  async setRound(round: string){
    const guesses = await this.getGuesses(round, this.state.currentUser.userToken)
    
    let currentMatches = leaguefixtures?.Match.filter(match => {
      return match.MatchRound === round
    });
    this.setState({guesses: guesses, round: round, matches: currentMatches}, () => {
      this.updateScoreBoard();
    });
    
  }

  setValue = (val: any) => {
    if(val === 1){
      this.updateScoreBoard();
    }
    this.setState({value: val})
  }

  render() {
    
    const { 
      showResults, 
      guesses, 
      mainRound, 
      round, 
      matches, 
      currentUser, 
      scoreBoard, 
      totalScoreBoard,
      value 
    } = this.state;

    return (
      <div className="App">
        <header className="App-header">
          <h1>Tippeligaen!</h1>
        </header>
        <div className="App-body">
        {
          (currentUser == null) ?  
          <SignIn onLogin={this.setUser}/> :
          
          
          
          <div className="white-text miip">
            { (value === 2) ? 
            <div> 
              <div className="userinfo"> 
                Brukernavn: {currentUser.userName}
              </div>
              <div>
                Epost: {currentUser.userEmail}
              </div>
              <div className="userinfo"> 
                <Button className="userinfo" variant="outlined" color="error" onClick={this.logout}>Logg ut</Button>
              </div>
            </div> 
            :''
            }
            { !(value === 2) ? 
            <div> 
              <div className="userinfo cam days white-text">
                <p>
                  Her er de {matches.length ?? 0} kampene {(mainRound===round) ? 'denne runden' : 'i runde ' + round}.
                </p>
                <div className="cam2">
                  <Button variant="contained" onClick={() => {this.minusRound()}}>Forrige runde</Button>
                  <Button variant="contained" onClick={() => {this.plusRound()}}>Neste runde</Button>
                </div>


              </div>


              { !(value === 1) ? 
              <div className="Match-body">
                {matches.map(match => (  
                  <MatchCard key={match.MatchID} match={match} roundId={round} mainRound={mainRound} />
                ))} 
              </div>
              :
              <div>
                <div className="Match-body">
                  <ScoreBoard totalScoreBoard={totalScoreBoard} />
                  {scoreBoard ? <ScoreBoard scoreBoard={scoreBoard} />: ''}
                  </div>

                <div className="Match-body">
                  {matches.map(match => (  
                    <Results key={match.MatchID} match={match} roundId={round} results={this.getResult(match, guesses)} />
                  ))} 
                </div>
              </div>
              }

            </div>
            :''}
          </div>
        }
        </div>
        <ThemeProvider theme={theme}>
          <Box sx={{ width: 500 }}>
            <BottomNavigation
              sx={{ position: 'fixed', bottom: 0, left: 0, right: 0, zIndex: 1000 }}
              showLabels
              value={value}
              onChange={(event, newValue) => {
                this.setValue(newValue);
              }}
            >
              <BottomNavigationAction label="Kamper" icon={<SportsSoccerIcon />} />
              <BottomNavigationAction label="Resultater" icon={<ContentPasteIcon />} />
              <BottomNavigationAction label="Konto" icon={<AccountCircleIcon />} />
            </BottomNavigation>
          </Box>
        </ThemeProvider>
      </div>
    );
  }
}

export default App;
